/* Declaring mixins */
%loginform_input{
    @extend %input_textbox;
    padding-left: 45px;
    border: 1px solid #ccc;
    box-shadow: inset 0 1px 1px rgba(0,0,0,.075);
    transition: border-color ease-in-out .15s,box-shadow ease-in-out .15s;
        &:focus {
            border-color: #66afe9;
            outline: 0;
            box-shadow: inset 0 1px 1px rgba(0,0,0,.075),0 0 8px rgba(102,175,233,.6);
            border-width: thin;
            border-top-style: double;
            border-left-style: double;
        }
}

/* Ends here */

#username{
    &.error {
        border-color: red;}
    @extend %loginform_input;
}
#email {
    &.error {
        border-color: red;}
    @extend %loginform_input;   
}
#password1 {
    &.error {
        border-color: red;}
    @extend %loginform_input;   
}
#cpassword {
    &.error {
        border-color: red;}
    @extend %loginform_input;   
}

.login-form__label {
    @extend %form_label1;
}

.login-form__login-text{
    color: $input_color_blue;
}

label.error{
    @extend %label_error;
}

/* Adding the Glyphicon */
.inner-addon {
    position: relative;
}
.inner-addon .glyphicon {
    position: absolute;
    padding: 10px;
    pointer-events: none;
}
.left-addon .glyphicon  { left:  0px;}
.right-addon .glyphicon { right: 0px;}

.left-addon input  { padding-left:  30px; }
.right-addon input { padding-right: 30px; }

.login-icon{
    font-size: 34px;
    color: $input_color_blue;
}
.progress {
    width: 90%;
}
/* End here */